import React, {useState} from 'react';
import axios from 'axios';

const App = () => {
  const [state,setState] = useState("")

  const getData = async () => {
    const res = await axios.get(state)
    console.log(res)
  }

  const postData = async () => {
    const res = await axios.post(state)
    console.log(res)
  }

  return (
    <div>
      <h1 onClick={getData}>get</h1>
      <h1 onClick={postData}>post</h1>
      <input onChange={(e) => {setState(e.target.value)}}></input>
    </div>
  )
}

export default App;
